import utlis from '../../static/js/utlis.js';
export default {
	data() {
		return {
			code: '获取验证码',
			prompt: '手机号输入有误，请重新输入',
			showPrompt: 0,
			phoneNum: '',
			password: '',
			passwordSec: '',
			smsCode: ''
		}
	},
	onLoad() {

	},
	methods: {
		onKeyInputPhone: function(event) {
			this.phoneNum = event.target.value
			console.log(this.phoneNum)
		},
		onKeyInputCode: function(event) {
			this.smsCode = event.target.value
			console.log(this.smsCode)
		},
		onKeyInputPsd: function(event) {
			this.password = event.target.value
			console.log(this.password)
		},
		onKeyInputPsdSec: function(event) {
			this.passwordSec = event.target.value
			console.log(this.passwordSec)
		},
		//获取验证码
		reCode() {
			let self = this;
			if (!utlis.checkMobile(this.phoneNum)) {
				uni.showToast({
					title: '手机号输入有误，请重新输入',
					duration: 2000,
					mask: true,
					icon: 'none'
				})
				return;
			}
			var time = 60
			if (self.code == "获取验证码" || self.code == "重新获取") {
				self.code = time + "s后重获"
				let data = {
					phone: self.phoneNum,
				}
				uni.showLoading({
					title: '加载中',
					mask: true
				})
				// utlis.http("POST", 'https://www.hjexyz.cn/lcyj-common-wap/newApp/smsSend', data, function(res) {
				utlis.http("POST", 'smsSend', data, function(res) {
					if (res.status == "success") {
						console.log(res)
						// 验证提示
						uni.hideLoading()
						uni.showToast({
							title: res.msg,
							duration: 2000,
							mask: true,
							icon: 'success'
						})
					} else {
						// 验证提示
						uni.hideLoading()
						uni.showToast({
							title: res.msg,
							duration: 2000,
							mask: true,
							icon: 'none'
						})
					}
				})
				var verification = setInterval(() => {
					time--;
					self.code = time + "s后重获";
					if (time == 0) {
						self.code = "重新获取";
						time = 60;
						clearInterval(verification)
					}
				}, 1000);
			}

		},
		// 提交
		showPro() {
			let self = this;
			if (!utlis.checkMobile(this.phoneNum)) {
				uni.showToast({
					title: '手机号输入有误，请重新输入',
					duration: 2000,
					mask: true,
					icon: 'none'
				})
				return;
			}
			if (this.smsCode == '') {
				uni.showToast({
					title: '验证码不能为空',
					duration: 2000,
					mask: true,
					icon: 'none'
				})
				return;
			}
			if (this.password == '' || this.passwordSec == '') {
				uni.showToast({
					title: '验证码不能为空',
					duration: 2000,
					mask: true,
					icon: 'none'
				})
				return;
			}
			if (this.password != this.passwordSec) {
				uni.showToast({
					title: '两次密码不一致，请重新输入',
					duration: 2000,
					mask: true,
					icon: 'none'
				})
				return;
			}
			let data = {
				forget: 1,
				custName: self.phoneNum,
				smsCode: self.smsCode,
				password: self.password
			}
			uni.showLoading({
				title: '加载中',
				mask: true
			})
			// utlis.http("POST", 'https://www.hjexyz.cn/lcyj-common-wap/newApp/appRegister', data, function(res) {
			utlis.http("POST", 'appRegister', data, function(res) {
				if (res.status == "success") {
					console.log(res)
					uni.setStorageSync('phoneNum', self.phoneNum)
					console.log(1)
					// 提示
					uni.hideLoading()
					uni.showToast({
						title: res.msg,
						duration: 2000,
						mask: true,
						icon: 'success'
					})
					setTimeout(() => {
						uni.navigateBack({
							delta: 1
						});
					}, 2000)
				} else {
					// 提示
					uni.hideLoading()
					uni.showToast({
						title: '修改失败',
						duration: 2000,
						mask: true,
						icon: 'none'
					})
				}
			})
		},
	}
}
